const usersNameList = (arr) => {
  let usersName = [];
  for (let i = 0; i < 10; i++) {
    usersName.push(arr[i].first_name);
  }
  return usersName;
};
console.log(usersNameList(userList));
const splitOnPages = (arr) => {
  let pages = [];
  for (let k = 0; k < arr.length / 50; k++) {
    let page = [];
    for (let i = 0 + 50 * k; i < 50 * (k + 1); i++) {
      // let page = [];
      // for (let k = 0; k < arr.length / 50; k++) {
      //  if (i / 50 < k + 1) {
      page.push(arr[i + k * 50]);
    }
    pages.push(page);
  }
  return pages[0];
};
console.log(splitOnPages(userList));
